// a class used by the tests to mimic the chrome storage
// it only implement the functionality of the functions used in the extension

export class ChromeStorage {
    constructor (storageType) {
        if (storageType == 'test') {
            return new ChromeStorageMock();
        } else if (storageType == 'browser') {
            return chrome.storage.local;
        }
    }
}

export class ChromeStorageMock {
    constructor () {
        this.storage = {};
    }

    set(obj, callback){
        
        for (let key in obj) {
            this.storage[key] = obj[key];
        }
        
        if (callback instanceof Function){
            callback();
            return;
        }
        
    }

    get(keys, callback){

        let result = {};
        if (typeof keys === 'string') {
            result = { [keys]: this.storage[keys] };
            callback(result);
            return;
        }  


        for (let key of keys) {
            if (key in this.storage){
                result[key] = this.storage[key];
            }
            
        }

        if (callback instanceof Function){
            callback(result);
            return;
        }
        
    }
    
    clear(callback) {
        this.storage = {};

        if (callback instanceof Function){
            callback();
            return;
        }
    }
}
