import {callbackErrorHandler, convertToDays, convertToMinutes, getSecondsDiff, SECONDS_IN_MINUTE} from './util.js';
const psl = require('psl');
const urlParser = require('url');

import {ChromeStorage} from './storage.js';

export default class Limiter {

    constructor(storageType, blockedDomains, allowedDomains) {
        this.storage = new ChromeStorage(storageType);

        this.blockedDomains = {};
        this.limitPerDomain = {};

        for (let blockedDomain of blockedDomains) {
            let domain = this.getHostname(blockedDomain.domain);
            this.blockedDomains[domain] = blockedDomain.name;
            this.limitPerDomain[domain] = blockedDomain.limit;
        }

        this.allowedDomains = {};
        for (let allowedDomain of allowedDomains) {
            this.allowedDomains[allowedDomain] = true;
        }

        this.isActive = true;

        this.isShowTimer = false;
        this.isShowReviewFooter = true;

        this.storage.get(['isShowTimer', 'isShowReviewFooter'], callbackErrorHandler((result) => {
            if (!result.isShowTimer){
                this.isShowTimer = false;
            } else {
                this.isShowTimer = true;
            }

            if('isShowReviewFooter' in result  ) {
                this.isShowReviewFooter = result.isShowReviewFooter;
            }
        }));
    }

    isDomainAllowed(domain) {
        if (domain in this.allowedDomains) {
            return true;
        }
        return false;
    }

    getBlockedUrlName(url) {
        let hostname = this.getBlockedSubdomain(url);
        return this.blockedDomains[hostname] || hostname;
    }

    getBlockedDomains() {
        var this_ = this;
        return new Promise((resolve, reject) => {
            this.storage.get(['blockedDomains'], callbackErrorHandler(function(resultBlockedDomains) {

                this_.getDailyStats (new Date()).then((resultDailyStat)=>{

                    if (!resultBlockedDomains.blockedDomains){
                        resolve(resultBlockedDomains.blockedDomains);
                    }

                    let blockedDomains = [];
                    for (let blockedDomain of resultBlockedDomains.blockedDomains) {
                        if ( blockedDomain.domain in resultDailyStat ) {
                            blockedDomain.timeLeftToday = convertToMinutes((blockedDomain.limit * SECONDS_IN_MINUTE) - resultDailyStat[blockedDomain.domain]);

                            if (blockedDomain.timeLeftToday < 0 ) {
                                blockedDomain.timeLeftToday = 0;
                            }
                        }

                        if (!('timeLeftToday' in blockedDomain)) {
                            blockedDomain.timeLeftToday = blockedDomain.limit;
                        }

                        blockedDomains.push(Object.assign({}, blockedDomain));
                    }
                    resolve(blockedDomains);
                });

            }));
        });
    }

    getIsShowTimer() {
        return this.isShowTimer;
    }

    setIsShowTimer(isShowTimer) {
        this.storage.set({ isShowTimer });
        this.isShowTimer = isShowTimer;
    }

    getIsShowReviewFooter() {
        return this.isShowReviewFooter;
    }

    setIsShowReviewFooter(isShowReviewFooter) {
        this.storage.set({ isShowReviewFooter });
        this.isShowReviewFooter = isShowReviewFooter;
    }

    getBlockedDomainsLocal() {
        return this.blockedDomains;
    }

    addBlockedDomain(domain, name=null, limit=null) {
        return new Promise((resolve, reject) => {

            domain = this.getHostname(domain);

            if (this.isDomainAllowed(domain)) {
                reject('Cannot add '+domain+' to the Pause list');
                return;
            }

            if (!domain || domain == "") {
                reject('Error adding website to pause list. Invalid domain: '+domain);
                return;
            }

            if (!name) {
                name = domain;
            }

            if (!limit) {
                limit = 25;
            }

            this.blockedDomains[domain] = name;

            this.getBlockedDomains().then( (blockedDomains) => {

                for (let blockedDomain of blockedDomains) {
                    if (blockedDomain.domain == domain) {
                        reject(domain+' is already present in the Limit list.');
                        return;
                    }
                }

                blockedDomains.push({ domain, limit });
                this.storage.set(
                    { blockedDomains },
                    callbackErrorHandler(() => {
                        this.limitPerDomain[domain] = limit;
                        resolve({});
                    }));
            });

        });
    }

    removeBlockedDomain(domain) {
        return new Promise((resolve, reject) => {
            delete this.blockedDomains[domain];
            delete this.limitPerDomain[domain];
            this.getBlockedDomains().then( (blockedDomains) => {
                let i = 0;
                for (let blockedDomain of blockedDomains) {
                    if (blockedDomain.domain == domain) {
                        blockedDomains.splice(i, 1);
                        break;
                    }
                    i++;
                }

                this.storage.set({ blockedDomains },
                    callbackErrorHandler(function(){
                        resolve();
                    })
                );
            });
        });
    }

    getHostname(url) {
        let urlParsed = urlParser.parse(String(url));

        if (!urlParsed.protocol) {
            // if the url doesn't have a protocol, add http://
            urlParsed = urlParser.parse('http://' + String(url));
        }

        if (!['http:', 'https:',].includes( urlParsed.protocol )) {
            return null;
        }

        if (urlParsed.hostname) {
            if (urlParsed.hostname.substring(0, 4) == 'www.'){
                return urlParsed.hostname.substring(4);
            } else {
                return urlParsed.hostname;
            }

        } else {
            return null;
        }
    }

    getSubdomains(url) {
        // returns all the possible subdomains from a hostname,
        // in order from the longest to the shortest
        // example: xyz.abc.com returns ['xyz.abc.com', 'abc.com', 'com']

        if (!url) {
            return [];
        }

        let hostname = this.getHostname(url);

        if (!hostname) {
            return [];
        }

        // return a full list of subdomains if the blockSubdomain flag is true
        let subdomains = [];

        let subdomain = '';
        for (let subdomainPart of hostname.split('.').reverse()) {
            subdomain = subdomainPart + '.' + subdomain;
            let subdomainSliced = subdomain.slice(0, -1);

            subdomains.push(subdomainSliced);
        }

        /* The order is important: we want to check the most-specific subdomain first.

        This allows a customer to provide different limits for more specific
        subdomains. For instance, `wikipedia.org` could have a general limit of
        10 minutes, but a shorter limit can be set for `en.wikipedia.org` with its
        own timer.
        */
        subdomains.reverse();
        return subdomains;
    }

    getBlockedSubdomain(url) {
        let subdomains = this.getSubdomains(url);

        for (let subdomain of subdomains) {
            if (subdomain in this.blockedDomains) {
                return subdomain;
            }
        };

        return null;
    }

    getBlockedDomainLimit(url){
        let hostname = this.getBlockedSubdomain(url);
        return this.limitPerDomain[hostname] || 10;
    }

    setLimitForDomain(domain, limit) {
        return new Promise((resolve, reject) => {

            if (limit < 0 || limit > 999 || isNaN(limit)) {
                reject('Limit must be between 0 and 999 minutes ');
                return;
            }

            this.limitPerDomain[domain] = limit;

            let blockedDomains = [];
            for (let domain in this.limitPerDomain) {
                blockedDomains.push({
                    domain,
                    limit: this.limitPerDomain[domain],
                    name: this.blockedDomains[domain],
                });
            }


            this.storage.set(
                { blockedDomains },
                callbackErrorHandler(function(){
                    resolve({});
                })
            );
        });
    }

    getLimitForDomain(domain) {
        return this.limitPerDomain[domain];
    }

    setActiveState(isActive){
        return new Promise((resolve, reject) => {
            this.isActive = isActive;
            this.storage.set({'extensionIsActive': isActive}, callbackErrorHandler(function(){
                resolve();
            }));
        });
    }

    getActiveState(){
        return this.isActive;
    }



    // track activity for a given url
    // stores the activities in local storage
    trackActivity(url, trackInterval) {
        let dailyStatForHostname = new Promise((resolve, reject) => {

            let date = new Date();
            var keyDailyStat = this.getDailyStatKey(date);
            let blockedSubdomain = this.getBlockedSubdomain(url);

            if (!blockedSubdomain) {
                resolve ({'hostname': null, 'seconds': 0});
                return;
            }

            // get the daily stats for this date
            this.storage.get([keyDailyStat],  callbackErrorHandler( (result) => {

                // an error gets reported that result is undefined
                if (typeof result === 'undefined' || result === null) {
                    resolve({ hostname: blockedSubdomain, seconds: 0 });
                    return;
                }

                let dailyStat = {};

                if(keyDailyStat in result) {
                    dailyStat = result[keyDailyStat];
                }

                // if we have stats for this date and hostname, increment it by trackInterval
                // else add this hostname as a new entry in the stats
                if (blockedSubdomain in dailyStat){
                    dailyStat[blockedSubdomain] += trackInterval;

                } else {
                    dailyStat[blockedSubdomain] = trackInterval;
                }

                // set the daily stats in storage
                this.storage.set({[keyDailyStat]: dailyStat});

                resolve({ hostname: blockedSubdomain, seconds: dailyStat[blockedSubdomain] });

            }));
        });

        return dailyStatForHostname;
    }

    // creates a storage entry key for a given date
    getDailyStatKey(date){

        var dd = date.getDate();
        var mm = date.getMonth() + 1;
        var yyyy = date.getFullYear();
        if (dd < 10) { dd = '0' + dd; }
        if(mm<10) { mm='0'+mm; }

        // ISO format tries to play smart with the timezones, so manually constructing the format
        return 'stat_'+yyyy+'-'+mm+'-'+dd;
    }


    // get the one day statistics from local storage
    // returns a promise
    getDailyStats (date) {

        let dailyStat = new Promise((resolve, reject) => {
            let keyDailyStat = this.getDailyStatKey(date);
            this.storage.get(keyDailyStat, callbackErrorHandler( function(result) {
                if (keyDailyStat in result){
                    resolve(result[keyDailyStat]);
                } else {
                    resolve([]);
                }

            }));
        });

        return dailyStat;
    }


    async sortGeneralStats (fullStats, limit){
        return new Promise((resolve, reject) => {
            let sortedStats = new Map();

            let i = 0;
            let otherSum = 0;

            var otherAdded = false;

            this.storage.get('excludedHosts',  callbackErrorHandler( (result) => {

                if (!result.excludedHosts) {
                    result['excludedHosts'] = {};
                }

                // sort data in descending order
                Object.keys(fullStats).sort(
                    function(a,b) {
                        return fullStats[b] - fullStats[a];
                    }
                ).map(key => {
                    if (i < limit) {
                        if (key in result['excludedHosts']) {
                            otherSum += fullStats[key];
                        } else {
                            sortedStats.set(key, fullStats[key]);
                            i++;
                        }
                    } else {
                        otherSum += fullStats[key];
                        sortedStats.set('other websites', otherSum);
                        otherAdded = true;
                        i++;
                    }
                });

                if (!otherAdded && otherSum>0) {
                    sortedStats.set('other websites', otherSum);
                }
                resolve(sortedStats);
            }));
        });
    }

    // get the statistics between two days
    // limit the results to top X sites. All others will be grouped under Other
    // returns a Map object within a promise (async function)
    async getGeneralStats (dateFrom, dateTo, limit=25) {


        if (!(dateFrom instanceof Date && dateTo instanceof Date)) {
            return(new Map());
        }

        let dateCheck = new Date(dateFrom.getTime());
        let fullStats = {};
        while (dateCheck <= dateTo) {

            let dailyStat = await this.getDailyStats(dateCheck);

            if (dailyStat){
                for (let key in dailyStat){
                    if (key in fullStats){
                        fullStats[key] += dailyStat[key];
                    } else {
                        fullStats[key] = dailyStat[key];
                    }

                }
            }

            // increment the dateCheck
            dateCheck.setDate(dateCheck.getDate() + 1);
            dateCheck.setHours(0,0,0,0);
        }

        let sortedStats = await this.sortGeneralStats(fullStats, limit);

        return sortedStats;
    }

    async getDetailedStats (dateFrom, dateTo, domain=null) {
        if (!(dateFrom instanceof Date && dateTo instanceof Date)) {
            return [];
        }

        let dateCheck = new Date(dateFrom.getTime());
        let fullStats = [];



        while (dateCheck <= dateTo) {
            let domainFoundForDate = false;
            let dailyStat = await this.getDailyStats(dateCheck);

            if (dailyStat){
                for (let key in dailyStat) {
                    if (key == domain){
                        fullStats.push({ 'date': new Date(dateCheck), 'timeSpent': dailyStat[key] });
                        domainFoundForDate = true;
                        break;
                    }

                }
            }

            if (!domainFoundForDate) {
                fullStats.push({ 'date': new Date(dateCheck), 'timeSpent': 0 });
            }

            // increment the dateCheck
            dateCheck.setDate(dateCheck.getDate() + 1);
            dateCheck.setHours(0,0,0,0);
        }

        // get the day difference between the start and end dates
        const diffTime = getSecondsDiff(dateFrom.getTime(), dateTo.getTime());
        const diffDays = convertToDays(diffTime);

        // if we have a date range greater than 2 months, group the data rows by week
        if (diffDays > 61) {
            let fullStatsByMonday = {};
            for (let dailyStat of fullStats) {

                let theDate = new Date(dailyStat.date);
                let day = theDate.getDay();
                let diff = theDate.getDate() - day + (day == 0 ? -6:1); // adjust when day is sunday
                theDate.setDate(diff);

                if (theDate in fullStatsByMonday){
                    fullStatsByMonday[theDate] += dailyStat.timeSpent;
                } else {
                    fullStatsByMonday[theDate] = dailyStat.timeSpent;
                }
            }

            fullStats = [];
            for (let monday in fullStatsByMonday) {
                fullStats.push({ 'date': new Date(monday), 'timeSpent': fullStatsByMonday[monday]});
            }
        }

        return fullStats;
    }
}
