import {callbackErrorHandler, convertToMinutes, SECONDS_IN_MINUTE} from '../lib/util.js';

import defaultBlockedDomains from '../lib/blocked_domains.js';

import Limiter from '../lib/limiter.js';

const INTERVAL_IN_MINUTES = 1;
const INTERVAL_IN_SECONDS = INTERVAL_IN_MINUTES * SECONDS_IN_MINUTE;

const IDLE_THRESHOLD_IN_SECONDS = 15;

var browser;

if (!browser) {
    browser = chrome;
}

const forbiddenURLs = [/chrome:\/\/.*/, /chrome-extension:\/\/.*/, /moz-extension:\/\/.*/];

const moment = require('moment');
require("moment-duration-format");
const querystring = require('querystring');

/* TODO: Refactor to use async/await when browser.idle.queryState is promisified on Chrome
    https://developer.chrome.com/docs/extensions/reference/idle/#method-queryState
*/
const getActiveTab = () => new Promise((resolve, reject) => {
    browser.windows.getCurrent(callbackErrorHandler((win) => {
        if (!win || !win.focused) {
            reject('Window is not focused');
        }

        browser.idle.queryState(IDLE_THRESHOLD_IN_SECONDS, callbackErrorHandler((state) => {
            if (state != 'active' && state != 'idle') {
                reject('User is not active or idle');
            }

            browser.tabs.query({ active: true, currentWindow: true }, callbackErrorHandler((tabs) => {
                // an error is reported when tabs is undefined
                if (typeof tabs === 'undefined' || tabs === null) {
                    reject('No active tab');
                }

                var activeTab = tabs[0];

                if (!activeTab) {
                    reject('No active tab');
                }

                // do not track if the state is idle and the tab has not produced audio recently
                // the audible property is true when a tab is playing audio
                // it is false when the video is muted within the player
                // it is true when the tab is muted from the browser menu
                if (state == 'idle' && activeTab.audible == false) {
                    reject('Tab is idle and not audible');
                }

                resolve(activeTab);
            }));
        }));
    }));
});

const sendMessageToTab = (tab, subject, params) => {
    // Skip some forbidden URLs like chrome://**, moz-extension://**, webstore, etc, where it fails.
    if (!tab || !tab.id || typeof subject !== 'string' || forbiddenURLs.some(url => url.test(tab.url))) {
        return;
    }
    if (typeof params === 'undefined') {
        params = {};
    }
    browser.tabs.sendMessage(tab.id, {
        subject,
        ...params,
    }).catch((error) => {
        // We catch the unhandled forbidden URLs here.
        if (error.message === 'Could not establish connection. Receiving end does not exist.') {
            /* If you face this warning, it's due to one of the two reasons:
                - The URL is protected by the browser from content script injection: add that URL to the forbiddenURLs array.
                - Context invalidation after reloading the extension without refreshing tabs, it's not handled yet in PR , there are two solutions
                    - Refresh the tabs
                    - Re-inject the content script and unload the old one programmatically (there isn't an API for this yet), yet there are some approaches to do this:
                        - https://stackoverflow.com/questions/10994324/chrome-extension-content-script-re-injection-after-upgrade-or-install/11598753#11598753
                        - https://stackoverflow.com/questions/53939205/how-to-avoid-extension-context-invalidated-errors-when-messaging-after-an-exte
            */
            console.warn(`Cannot send message of subject ${subject} to tab of id ${tab.id} and URL ${tab.url}. If the URL is part of the browser's protected URLs, please add it to the forbiddenURLs array to not see this warning, otherwise refresh that tab or consult the notice on background.js:36.`);
            return;
        };
        console.error(error);
    });
};

const getTabsWithDomain = async (domain) => {
    const tabs = await browser.tabs.query({});
    return tabs.filter((tab) => limiter.getBlockedSubdomain(tab.url) === domain || domain === '*');
};

const sendMessageToTabsWithDomain = async (domain, subject, params) => {
    const tabs = await getTabsWithDomain(domain);
    tabs.forEach((tab) => sendMessageToTab(tab, subject, params));
};

let browserRuntimeId;
try{
    browserRuntimeId = browser.runtime.id;
} catch (e) {
    browserRuntimeId = null;
}

var allowedDomains = [
    "ljfdccdjpfjpfjbpdiihanpodilolofh",
    "freedom.to"
];

allowedDomains.push(browserRuntimeId);

var limiter;

browser.alarms.get('limit-extension-alarm', function(alarm) {
    if (!alarm) {
        browser.alarms.create('limit-extension-alarm', {
            delayInMinutes: INTERVAL_IN_MINUTES,
            periodInMinutes: INTERVAL_IN_MINUTES,
        });
    }
});

browser.alarms.onAlarm.addListener(function(alarm) {
    if (alarm.name !== 'limit-extension-alarm') {
        return;
    }

    trackActivity(INTERVAL_IN_SECONDS)
        // HACK: unresolved promise kills the service worker if not caught
        .catch((error) => {
            console.error(error);
        });
});

// get the allowed and blocked domains from storage and initialize the Url Inspector
// allowed domains need to be in storage in case the extension is turned off and on again
var initializeExtension = function() {
    return new Promise((resolve, reject) => {

        browser.storage.local.get([
            'blockedDomains','extensionIsActive'
        ], callbackErrorHandler(function(result){

            if (typeof result === 'undefined') {
                result = {};
            }
            // initialized blocked domains as default list (first install)
            // if they are present in storage, use the list from storage
            let blockedDomains = defaultBlockedDomains;
            let extensionIsActive = true;

            if ('blockedDomains' in result) {
                blockedDomains = result.blockedDomains;
            } else {
                browser.storage.local.set({ blockedDomains });
            }

            if('extensionIsActive' in result) {
                extensionIsActive = result.extensionIsActive;
            }

            limiter = new Limiter('browser', blockedDomains, allowedDomains);

            if (!extensionIsActive){
                limiter.setActiveState(false).then(()=>{
                    browser.action.setTitle({'title':'Limit is disabled. Click to start limiting distracting sites.'});
                    browser.action.setIcon({path: '/images/limit_icon_off_48.png'});
                });
            }

            resolve();
        }));
    });
};

var resolveMessage = function (msg, sender, msgCallback) {
    if (msg.subject === 'getTimer') {
        trackActivity(0)
            // HACK: unresolved promise kills the service worker if not caught
            .catch((error) => {
                console.error(error);
            });
    } else if (msg.subject === 'excludeHostnameFromReport') {
        // allow domain once, for specific tab (continue button)
        limiter.excludeHostnameFromReport(msg.hostname).then(result => {
            msgCallback();
        });
        return true;

    } else if (msg.subject === 'getBlockedDomains' ) {
        limiter.getBlockedDomains().then((result)=>{
            msgCallback(result);
        });
        return true;

    } else if (msg.subject === 'addBlockedDomain' ) {
        limiter.addBlockedDomain(msg.domain).then(
            function () {
                msgCallback({});
            },
            function (error) {
                msgCallback({ error });
            }
        );
        return true;

    } else if (msg.subject === 'removeBlockedDomain' ) {
        limiter.removeBlockedDomain(msg.domain).then(() => {
            msgCallback();
        });

        return true;
    } else if (msg.subject === 'setLimitForDomain' ) {
        limiter.setLimitForDomain(msg.domain, msg.limit).then(
            function () {
                msgCallback({});
            },
            function (error) {
                msgCallback({ error });
            }
        );
        return true;
    } else if (msg.subject === 'getIsShowTimer' ) {
        // show timer only if Limit is active and the Timer is turned on
        msgCallback(limiter.getIsShowTimer());
        return;

    } else if (msg.subject === 'setIsShowTimer' ) {
        limiter.setIsShowTimer(msg.isShowTimer);

        // show timer only if Limit is active and the Timer is turned on
        let isShowTimer = limiter.getActiveState() && limiter.getIsShowTimer();

        // notify all tabs to hide or show the timer
        sendMessageToTabsWithDomain('*', 'showTimer', {
            isShowTimer,
        });

        return;
    } else if (msg.subject === 'setIsShowReviewFooter' ) {
        limiter.setIsShowReviewFooter(msg.isShowReviewFooter);
        return;
    } else if (msg.subject === 'toggleOnOff' ) {
        var toggle = !limiter.getActiveState();

        if(toggle){
            limiter.setActiveState(true).then(()=>{
                browser.action.setTitle({'title':'Limit is enabled.'});
                browser.action.setIcon({path: '/images/limit_icon_48.png'});
                msgCallback(limiter.getActiveState());

                sendMessageToTabsWithDomain('*', 'extensionActive');
            });
        }
        else{
            limiter.setActiveState(false).then(()=>{
                browser.action.setTitle({'title':'Limit is disabled. Click to start limiting distracting sites.'});
                browser.action.setIcon({path: '/images/limit_icon_off_48.png'});
                msgCallback(limiter.getActiveState());

                sendMessageToTabsWithDomain('*', 'extensionInactive');
            });
        }
        return true;
    } else if (msg.subject === 'getActiveState') {
        msgCallback(limiter.getActiveState());

    } else if (msg.subject === 'getPausePageData') {
        let blockedUrlName = limiter.getBlockedUrlName(msg.url);
        let isShowReviewFooter = limiter.getIsShowReviewFooter();

        msgCallback({ blockedUrlName, isShowReviewFooter });
    } else if (msg.subject === 'isUrlAllowed') {
        if (sender.tab) {
            trackActivity(0, sender.tab)
            .then(result => {
                if (!result) {
                    msgCallback(true);
                    return;
                }

                if (!limiter.getActiveState()) {
                    msgCallback(true);
                    return;
                }

                let allowedMinutesForDomain = limiter.getLimitForDomain(result.hostname);
                if (allowedMinutesForDomain === undefined) {
                    msgCallback(true);
                    return;
                }

                let allowedSecondsForDomain = allowedMinutesForDomain * SECONDS_IN_MINUTE;
                if (result.seconds >= allowedSecondsForDomain) {
                    msgCallback(false);
                    return;
                } else {
                    msgCallback(true);
                    return;
                }
            })
            // HACK: unresolved promise kills the service worker if not caught
            .catch((error) => {
                console.error(error);
            });
            return true;
        }
    }
};

const trackActivity = async (intervalInSeconds, tab) => {
    try {
        if (!limiter) {
            await initializeExtension();
        };

        let selectedTab = tab || await getActiveTab();

        if (typeof selectedTab === 'undefined') {
            throw 'No active tab found.';
        };

        const result = await limiter.trackActivity(selectedTab.url, intervalInSeconds);

        let timeSpentString = moment.duration(result.seconds, 'seconds').format("H[h] m[m]");
        let title;
        let isActive = limiter.getActiveState();

        if (isActive) {
            title = 'Limit is enabled.';
        } else {
            title = 'Limit is disabled.';
        }

        if (result.hostname) {
            title += '\nTime spent on ' + result.hostname + ' today: ' + timeSpentString;
        } else {
            title += '\nNot tracking activity for this website';
        }

        let siteName = limiter.getBlockedUrlName(selectedTab.url);

        const allowedMinutesForDomain = limiter.getLimitForDomain(result.hostname);

        const allowedSecondsForDomain = allowedMinutesForDomain * SECONDS_IN_MINUTE;

        const secondsLeft = allowedSecondsForDomain - result.seconds;
        const minutesLeft = convertToMinutes(secondsLeft);

        // set the timer if Limit is active, the website is in the blocklist, and the timer is turned on
        if (isActive && limiter.getLimitForDomain(result.hostname) && limiter.getIsShowTimer()) {
            sendMessageToTabsWithDomain(result.hostname, 'setTimer', {
                timeSpent: result.seconds,
                timeLeft: secondsLeft,
                domain: siteName,
            });
        } else {
            // Hide timer if the website is unblocked or the timer is turned off
            sendMessageToTabsWithDomain(result.hostname, 'showTimer', {
                isShowTimer: false,
            });
        }

        if (!isActive) {
            browser.action.setTitle({ title });
            throw 'Limit is disabled.';
        }

        if (!allowedMinutesForDomain) {
            browser.action.setTitle({ title });
            throw 'Not tracking activity on the selected tab\'s website.';
        }

        if (result.hostname) {
            let secondsLeftString = moment.duration(secondsLeft, 'seconds').format("H[h] m[m]");
            title += '\nTime left on ' + result.hostname + ' today: ' + secondsLeftString;
        }

        browser.action.setTitle({ title });

        if (result.seconds >= allowedSecondsForDomain) {

            const tabs = await getTabsWithDomain(result.hostname);

            tabs.forEach((tab) => {
                let qString = querystring.stringify({ url: selectedTab.url });

                browser.tabs.update(tab.id, {
                    'url': browser.runtime.getURL('html/limit.html') + "?" + qString
                });
            });
        } else {

            // show notification if the user has [15,10,5,2,1] minutes left on the site
            if ([15, 10, 5, 2, 1].indexOf(minutesLeft) > -1) {
                browser.notifications.clear('limitTimeWarningNotification');

                let siteName = limiter.getBlockedUrlName(selectedTab.url);

                browser.notifications.create('limitTimeWarningNotification', {
                    'type': 'basic',
                    'iconUrl': browser.runtime.getURL('images/limit_icon_128.png'),
                    'title': 'Limit by Freedom',
                    'message': `You have ${minutesLeft} minutes left on ${siteName}`
                });

            };
        };

        return result;
    } catch (error) {
        if (error instanceof Error) {
            throw error;
        }
        console.debug(error);
    };
};

initializeExtension();

// message listener
// listen from various messages from pages and content scripts
browser.runtime.onMessage.addListener( callbackErrorHandler( function (msg, sender, msgCallback) {
    if (!limiter) {
        // if inspector is not initialized, initialize it, and then resolve the message
        initializeExtension().then(function() {
            resolveMessage(msg, sender, msgCallback);
        });
        return true;
    } else {
        return resolveMessage(msg, sender, msgCallback);
    }

}));

browser.runtime.setUninstallURL("https://freedom-app.typeform.com/to/KvYEZp#ext=limit");
